import React, { useState, useEffect } from "react";
import axios from "axios";

export default function WeatherPage() {
  const [events, setEvents] = useState([]);
  const [loading, setLoading] = useState(true);
  const [page, setPage] = useState(1);
  const perPage = 5;
  const apiKey = "NO47MajqqasZv5SlhkVAjKxrp8cVyxEMuPX2VEkE";

  useEffect(() => {
    async function fetchData() {
      try {
        const endpoints = ["GST", "CME", "IPS", "FLR"].map(
          (type) =>
            `https://api.nasa.gov/DONKI/${type}?startDate=2024-06-01&endDate=2024-06-30&api_key=${apiKey}`
        );
        const responses = await Promise.all(endpoints.map((url) => axios.get(url)));
        const data = responses.flatMap((r) => r.data);
        data.sort((a, b) => new Date(b.submissionTime) - new Date(a.submissionTime));
        setEvents(data);
      } catch (err) {
        console.error(err);
      } finally {
        setLoading(false);
      }
    }
    fetchData();
  }, [apiKey]);

  const total = Math.ceil(events.length / perPage);
  const current = events.slice((page - 1) * perPage, page * perPage);

  return (
    <section className="min-h-screen bg-gradient-to-br from-indigo-50 to-white p-8">
      <div className="max-w-4xl mx-auto space-y-6">
        <header className="text-center space-y-2">
          <h1 className="text-4xl font-extrabold text-gray-900">
            Space Weather Alerts
          </h1>
          <p className="text-gray-600">
            Live updates on geomagnetic storms, CMEs, shocks and flares.
          </p>
        </header>

        {loading ? (
          <div className="flex justify-center py-12">
            <div className="animate-spin rounded-full h-16 w-16 border-4 border-indigo-600 border-t-transparent"></div>
          </div>
        ) : (
          <div className="grid gap-6">
            {current.map((ev, i) => (
              <article
                key={i}
                className="bg-white rounded-xl shadow hover:shadow-lg transition p-6 space-y-3"
              >
                <h2 className="text-2xl font-semibold">
                  {ev.gstID
                    ? "Geomagnetic Storm"
                    : ev.cmeID
                    ? "Coronal Mass Ejection"
                    : ev.ipsID
                    ? "Interplanetary Shock"
                    : ev.flrID
                    ? "Solar Flare"
                    : "Unknown Event"}
                </h2>
                <p>
                  <strong>Start:</strong> {new Date(ev.startTime).toUTCString()}
                </p>
                {ev.linkedEvents?.length > 0 && (
                  <p>
                    <strong>Linked IDs:</strong> {ev.linkedEvents.map((le) => le.activityID).join(", ")}
                  </p>
                )}
                <div className="flex justify-between items-center">
                  <a
                    href={ev.link}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="text-indigo-600 font-medium hover:underline"
                  >
                    View Report
                  </a>
                  <time className="text-gray-500 text-sm">
                    {new Date(ev.submissionTime).toUTCString()}
                  </time>
                </div>
              </article>
            ))}
          </div>
        )}

        {!loading && (
          <nav className="flex justify-center items-center space-x-4">
            <button
              onClick={() => setPage((p) => Math.max(p - 1, 1))}
              disabled={page === 1}
              className="px-3 py-1 rounded-full bg-white shadow hover:bg-indigo-100 disabled:opacity-50"
            >
              ←
            </button>
            <span className="text-gray-700">
              {page} / {total}
            </span>
            <button
              onClick={() => setPage((p) => Math.min(p + 1, total))}
              disabled={page === total}
              className="px-3 py-1 rounded-full bg-white shadow hover:bg-indigo-100 disabled:opacity-50"
            >
              →
            </button>
          </nav>
        )}

        <p className="text-sm text-gray-500 text-center">
          If no data appears, ensure your API key is valid and the CORS policy allows requests.
        </p>
      </div>
    </section>
  );
}
